@use "../abstracts/variables" as *;
.btn{

    &:link,
    &:visited{
        text-transform: uppercase;
        padding:1rem 4rem;
        border-radius: 2rem;
        font-weight: 600;
        display: inline-block;
      transition: all 0.5s;

    }

    &:hover{
        transform: translateY(-1rem);
box-shadow: 0 10px 20px rgba($clr-black, 0.4);

    }

    &:active{
        transform: translateY(-0.5rem); 
    box-shadow: 0 5px 10px rgba($clr-black, 0.2);

    }

    &--white{
        background-color: #fff;
        color:$clr-txt;
    

    }
}





