@use '../abstracts/variables' as *;
.row{
    max-width:$grid-width;
    margin-inline: auto;
   
    background-color: rgb(239, 239, 229);
   
    
&:not(:last-child){
    margin-bottom: $gutter-vertical;
}

}

.col-1-of-2{
    background-color: red;
    width: calc((100% - $gutter-horizontal)/2 );
}