@use "../base/animations" as *;



.heading--primary{

    &--main,
    &--sub{
        text-align: center;
        text-transform:uppercase;
      
        display: block;
       

    }
    &--main{

        font-size:3.6rem;
        letter-spacing:1rem;
        font-weight: 400;
        animation: moveInLeft 1s linear;
    }

    &--sub{
        font-size: 1.6rem;
        letter-spacing: 1.2rem;
        font-weight: 700;
        animation: moveInRight 1s linear;
    }
}

.heading--secondary{
    font-size: 3.4rem;
    text-transform: capitalize;
    font-weight: 700;



}

